.Header_hamburger__3ulik {
  width: 30px;
  position: relative;

  transform: rotate(0deg);
  transition: 0.5s ease-in-out;
  cursor: pointer;
}

.Header_hamburger__3ulik span {
  display: block;
  position: absolute;
  height: 4px;
  width: 100%;
  background: rgb(56, 55, 55);
  border-radius: 9px;
  opacity: 1;
  left: 0;
  transform: rotate(0deg);
  transition: 0.4s ease-in-out;
}

.Header_hamburger__3ulik span:nth-child(1) {
  top: 0px;
}

.Header_hamburger__3ulik span:nth-child(2) {
  top: 10px;
}

.Header_hamburger__3ulik span:nth-child(3) {
  top: 20px;
}

.Header_hamburger__3ulik.Header_open__xHuKo span:nth-child(1) {
  top: 8px;
  transform: rotate(135deg);
}

.Header_hamburger__3ulik.Header_open__xHuKo span:nth-child(2) {
  opacity: 0;
  left: -60px;
}

.Header_hamburger__3ulik.Header_open__xHuKo span:nth-child(3) {
  top: 8px;
  transform: rotate(-135deg);
}
.Header_navcontentVertical__30QBf {
  text-align: center;
  transform: rotate(360deg);
  transition: 1s ease-in-out;
}
.Header_navcontentVertical__30QBf p {
